<template>
  <transition name="slider">
    <music-list :songs="songList" :bgurl="bgurl" :title="title" :rank='rank'></music-list>
  </transition>
</template>

<script type="text/javascript">
  import {getTopList, getMusicList} from 'api/rank'
  import { ERR_OK } from 'api/config'
  import { mapGetters, mapMutations, mapActions } from 'vuex'
  import { creatSong, processSongsUrl} from 'common/js/song'
  import musicList from '../music-list/musicList'
  export default {
    data() {
      return {
        songList: [],
        rank: true
      }
    },
    computed: {
      ...mapGetters(['currentTopList']),
      bgurl() {
        if (!this.songList.length) {
          return
        }
        return this.songList[0].image
      },
      title() {
        console.log(this.currentTopList)
        return this.currentTopList.topTitle
      }
    },
    components: {
      musicList
    },
    methods: {
      _normalLizeSong(musicData) {
        let songItem = creatSong(musicData)
        this.songList.push(songItem)
      }
    },
    created () {
      getMusicList(this.currentTopList.id).then(res => {
        if (res.code === ERR_OK) {
          let list = res.songlist
          list.forEach((item) => {
            this._normalLizeSong(item.data)
          })
          processSongsUrl(this.songList)
        }
      })
    },
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "../../common/stylus/variable"
  .slider-enter-active,.slider-leave-active{
    transition: all .3s
  }
  .slider-enter,.slider-leave-to{
    transform: translate3d(100%, 0, 0)
  }
</style>


